<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      form: {},
      loading: false,
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/info', {
      type: 'checkin',
    })
      .then(r => {
        this.form = r.data
      })
      .catch(res => {
        Message.error(res.msg)
      })

    this.loading = false
  },
  methods: {
    onSubmit() {
      Fetch('/sys/config/update', {
        type: 'checkin',
        ...this.form,
      })
        .then(() => {
          Message.success('操作成功')
        })
        .catch(() => {
          Message.error('操作失败')
        })
      this.loading = false
    },
  },
}
</script>
